
#simple-query { display: flex ; justify-content: space-between; align-items: flex-start; margin-top: 50px}
#simple-query .half{flex: 1 1; padding: 0 10px 0 10px;}
#simple-query textarea {
    width: 100%;
    font-size: 22px;
    padding: 5px 10px 0 5px;
}

#simple-query .rjv-wrapper {
    text-align: left;
    background-color: #fff;
    font-size: 0.75em;
}
.d-none {display: none;}
.d-flex {display: flex}
.flex-one{flex: 1 1}
.flex-column {flex-direction: column}
.justify-content-around {justify-content: space-around}
.justify-content-center {justify-content: center}


.p-4 {padding: 40px;}
.pt-2 {padding-top: 20px;}

.mt-2 {padding-top: 20px;}
.mt-5 {padding-top: 50px;}

#table-demo table{
    color: #484848;
    width: 100%;
}

#table-demo th, td {
    border: 1px solid black;
}

#table-demo th {
    background-color: #000;
    color: #fff;
    font-weight: bold;
}

#table-demo .editor {font-size: 16px;}
#table-demo .preview table{overflow-y: scroll !important;}
